import axios from "../axios";

export default {
    namespaced: true,
    state () {
        return {
            profile: {},
        }
    },
    mutations: {
        getProfile(state, profile)
        {
            state.profile = profile
        },
    },
    actions: {
        getProfile({commit}, param = {}) {
            console.log('sdf',param.profileId)
            axios().get(`/api/my-profile/${param.profileId}`)
            .then( response => {
                commit('getProfile', response.data);
                console.log('sdf', response.data)
            });
        },
    }
}